<template>
    <!--begin::Modal-->  
    <div class="modal fade" id="formModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel" >{{form.id ?$t('general.update'):$t('general.create')}}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body row">
                     
                                         
                                        <div class="col-12 mt-3" v-if="categories&&categories.length" >
                        <label>{{$t('title.category')}}</label>
                        <v-select dir="rtl" label="name" class="vselect" style="width: 100%;"
                        :options="categories" :reduce="val => val.id" v-model="form.category_id"></v-select> 
                    </div> 
                                         
                                         
                                         
                                         
                                         
                                                             
                                         
                                         
                                            <div class="col-12 mt-3">
                            <label>{{$t('inf.title')}}</label>
                            <input v-model="form.title" type="text" autocomplete="off"
                            class="form-control" :class="{ 'is-invalid': form.errors.has('title') }">
                            <has-error :form="form" field="title"></has-error>
                        </div> 
                                         
                                            <div class="col-12 mt-3">
                            <label>{{$t('inf.image')}}</label>
                            <input v-model="form.image" type="text" autocomplete="off"
                            class="form-control" :class="{ 'is-invalid': form.errors.has('image') }">
                            <has-error :form="form" field="image"></has-error>
                        </div> 
                                         
                                         
                                         
                                            <div class="col-12 mt-3">
                            <label>{{$t('inf.content')}}</label>
                            <input v-model="form.content" type="text" autocomplete="off"
                            class="form-control" :class="{ 'is-invalid': form.errors.has('content') }">
                            <has-error :form="form" field="content"></has-error>
                        </div> 
                                                            
                </div> 
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal" >{{$t("general.close")}}</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary" >{{$t('inf.status')}}</button>
                    <button v-else type="submit" class="btn btn-primary" >{{$t("general.save")}}</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>

<script>
import {Form,HasError} from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";


export default {
    components: {HasError},
    props: ['fetchData'],
    data() {
        return {
            form: new Form({id:null,
             
                         
                            category_id:null,
                         
                            title:null,
                         
                            image:null,
                         
                         
                         
                            content:null,
                                    }),
             
                         
                            categories:null,
                         
                         
                         
                         
                         
                                };
    }, 
    created() {
        this.getInfo();
    },
    methods: {
        create(){
            this.form.reset();
            $('#formModal').modal('show');
        },
        getInfo(){ 
             
                         
                        axios.get("/api/categories",{params: {paginate:0} }).then(({data}) => {
                this.categories=data.success.categories;
            }).catch((error) => {console.log("Error......")})
                         
                         
                         
                         
                         
                                },  
        store(){ 
            this.form.post("/api/articles").then(({data}) => {
                if (data.message) {
                    this.$swal.fire({icon: 'error',title: "Error Message",text: data.message+'!'});
                }else {
                    this.fetchData();
                    $('#formModal').modal('hide');
                }
            }).catch((error) => {console.log("Error......")})
        },  
        edit(form){ 
            this.form.fill(form);
            $('#formModal').modal('show');
        },
        update(){
            this.form.put("/api/articles/"+this.form.id).then((res) => {
                this.fetchData();
                $('#formModal').modal('hide');
            }).catch((error) => {console.log("Error......")})
        },  
        destroy(id) {
            this.$swal.fire({
                title:this.$t("general.warning"),text:this.$t("general.r_u_s"),
                icon: 'warning',showCancelButton: true,confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',cancelButtonText: this.$t("general.cancel"),
                confirmButtonText: this.$t("general.yes_delete"),
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/articles/'+id).then(response => {
                        if(response.data.message){
                            this.$swal.fire({icon: 'error',title: 'Oops...',text: response.data.message+'!'});
                        }else{
                            this.$swal.fire(this.$t("general.deleted"),this.$t("general.delete_success"),'success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({icon: 'error',title: 'Oops...',text: 'Something went wrong!'}); 
                    });
                }
            })
        },
    },
}
</script>
