<template>
    <layout-default>
        <div class="page-content">
            <div class="container-fluid">
                
                <!-- start page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="page-title-box d-flex align-items-center justify-content-between">
                            <h4 class="mb-0">{{$t("general.table",[$t("title.articles")])}}</h4>
                            <div class="page-title-right">
                                <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                                    + {{$t('general.createNew')}}
                                </button>
                                <form-modal ref="FormModal" :fetchData="fetchData"/>
                            </div>
                            
                        </div>
                    </div>
                </div>
                <!-- end page title -->
                
                <div class="row">
                    <div class="col-12">
                        <div class="card">
                            <div v-if="articles&&articles.data.length" class="card-body">
                                <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                                    <thead>
                                        <tr>
                                                                                                                                                                                                                            <th>{{ $t('title.category') }}</th>
                                                                                                                                                                                <th>{{ $t('inf.title') }}</th>
                                                                                                                                                                                <th>{{ $t('inf.image') }}</th>
                                                                                                                                                                                <th>{{ $t('inf.created_at') }}</th>
                                                                                                                                                                                                                                                                        <th>{{ $t('inf.content') }}</th>
                                                                                                                                    <th>{{$t('general.options')}}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(article,index) in articles.data">
                                                                                                                                                                                                                                                                        <td>{{article.category.name}}</td>
                                                                                                                                                                                                                                                                        <td>{{article.title}}</td>
                                                                                                                                                                                                                                                                        <td>{{article.image}}</td>
                                                                                                                                                                                                                                                                        <td>{{article.created_at| moment("DD/MM/YYYY")}}</td>
                                                                                                                                                                                                                                                                                                                                                                <td>{{article.content}}</td>
                                                                                                                                                                                <td>
                                                <div class="btn-group">
                                                    <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                        {{$t('general.more')}} <i class="mdi mdi-chevron-down"></i>
                                                    </button>
                                                    <div class="dropdown-menu" style="min-width: 120px;">
                                                        <router-link :to="{ name: 'article_show', params: { article_id: article.id } }" tag="a" class="dropdown-item">
                                                            <i class="far fa-eye"></i> {{$t("general.show")}}
                                                        </router-link>
                                                        <a @click="$refs.FormModal.edit(article)" class="dropdown-item dropdownItem" href="javascript:;">
                                                            <i class="far fa-edit"></i> {{$t("general.edit")}}
                                                        </a>
                                                        <a @click="$refs.FormModal.destroy(article.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                                            <i class="far fa-trash-alt"></i> {{$t("general.delete")}}
                                                        </a>
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                                <pagination-nav v-if="articles.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                                </div>
                                <div v-else class="card-body">
                                    <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                                    <p v-else class="card-title-desc" style="text-align: center;">
                                        {{$t("general.y_d_h",[$t("title.articles")])}}
                                    </p>
                                </div>
                            </div>
                        </div> <!-- end col -->
                    </div> <!-- end row -->
                </div> <!-- container-fluid -->
            </div>
        </layout-default>
    </template>  
    
    
    <script>
        import LayoutDefault from '../../layouts/App';
        import PaginationNav from '../../components/pagination';
        import FormModal from '../../components/modals/article/add';
        import axios from 'axios';
        
        export default {
            components: {LayoutDefault,PaginationNav,FormModal},
            data() {
                return { articles:null,loader:false}
            },  
            created() {
                this.fetchData();
            },
            methods: {
                fetchData(num=1) {
                    this.articles=null;
                    this.loader=true;
                    let data = {params: {page:num} };
                    axios.get('/api/articles',data).then(({data}) => {
                        this.articles=data.success.articles;
                        if(!data.success.articles.data.length&&num!=1){
                            this.fetchData(1)
                        }           
                        this.loader=false;
                    });  
                },
            },
        }
    </script>